<template>
  <div>
    <Modal
      width="350"
      v-model="isOpen"
      :title="title"
      @on-visible-change="openOrClose"
      :mask-closable="false"
    >
        <Form ref="consumeForm" :model="consumeInfo" :rules="ruleConsumeInfo" :label-width="80">
            <FormItem label="" prop="consumeItemCode">
                <Select v-model="consumeInfo.consumeItemCode" placeholder="" @on-change="getTips">
                    <Option value="rent_manual"></Option>
                    <Option value="commodity"></Option>
                    <Option value="cleaning_charge"></Option>
                    <Option value="damage"></Option>
                    <Option value="other_consume"></Option>
                </Select>
            </FormItem>
            <FormItem label="" prop="consumeItemDetail">
                <Input v-model="consumeInfo.consumeItemDetail" :placeholder="tips"></Input>
            </FormItem>
            <FormItem label=" " prop="roomNo">
                <Select v-model="consumeInfo.roomNo" placeholder="" @on-change="setRoomName">
                    <Option v-for="(item, index) in orderRooms" :key="index" :value="item.roomNo">{{ item.roomName }}</Option>
                </Select>
            </FormItem>
            
            <FormItem label=" " prop="consumeMoney">
                <Input v-model="consumeInfo.consumeMoney" placeholder="" number></Input>
            </FormItem>
            <FormItem label=" " >
                <Input v-model="consumeInfo.consumeComment" placeholder="" type="textarea"></Input>
            </FormItem>
        </Form>
         <div slot="footer">
          <Button
            size="large"
            @click="isOpen = false;"
          ></Button>
          <Button
            type="primary"
            size="large"
            :loading="loading"
            @click="submit"
          ></Button>
       </div>
    </Modal>

    <Modal
      width="350"
      v-model="isOffsetOpen"
      title=""
      @on-visible-change="openOrClose"
      :mask-closable="false"
    >
      <Form ref="offsetForm" :model="consumeInfo" :rules="ruleOffsetInfo" :label-width="80">
            <FormItem :label-width="0">
                <h3>{{ offsetOrderConsume&&offsetOrderConsume.consumeMoney?offsetOrderConsume.consumeMoney:'' }}  &nbsp </h3>
            </FormItem>
            <FormItem label="" prop="consumeMoney">
                <Input v-model="consumeInfo.consumeMoney" placeholder="" number></Input>
            </FormItem>
            <FormItem label=""  prop="consumeComment">
                <Input v-model="consumeInfo.consumeComment" placeholder="" type="textarea"></Input>
            </FormItem>
        </Form>

        <div slot="footer">
          <Button
            size="large"
            @click="isOffsetOpen = false;"
          ></Button>
          <Button
            type="primary"
            size="large"
            :loading="loading"
            @click="offsetSubmit"
          ></Button>
       </div>
    </Modal>
  </div>
</template>

<script>
import {saveOrderConsume} from '@/api/order.js'
export default {
  name: "ConsumeEntry",
  props: {
    openIs: Boolean,
    orderRooms: Array,
    openIsOffset: Boolean,
    offsetOrderConsume: Object
  },
  data() {
    return {
      isOpen: false,
      isOffsetOpen: false,
      title: '',
      loading: false,
      tips: '',
      saveIs: false,
      consumeInfo: {
          orderNo: '',
          consumeTypeCode: 'income',
          consumeItemCode: '',
          consumeItemDetail: '',
          consumeMoney: null,
          roomName: null,
          roomNo: null,
          consumeComment: null,
          offsetId: null,
      },
      ruleConsumeInfo: {
          consumeItemCode: [
              { required: true, message: '',}
          ],
          consumeItemDetail: [
              { required: true, message: '',}
          ],
          roomNo: [
              { required: true, message: '',}
          ],
          consumeMoney: [
              { required: true, message: '', },
              { type: "number", min: 1, message: "0",},
          ],
      },
      ruleOffsetInfo: {
        consumeMoney: [
              { required: true, message: '', },
              { type: "number", min: 1, message: "0",},
          ],
        consumeComment: [
            { required: true, message: '', },
        ],
      }
    };
  },
  watch: {
    openIs(openIs) {
      this.isOpen = openIs;
    },
    openIsOffset(openIsOffset) {
      this.isOffsetOpen = openIsOffset;
    },
  },
  methods: {
      openOrClose(val) {
      if (!val) {
        
        
        this.$emit("consume-entry-close", this.saveIs);
        // 
        console.log(this.$options.data());
        
        Object.assign(this.$data, this.$options.data());
        
      } else {
        console.log('offsetOrderConsume:',this.offsetOrderConsume);
        
        this.$refs.consumeForm.resetFields();
        this.title += '--' + this.orderRooms[0].orderNo; 
        this.consumeInfo.orderNo = this.orderRooms[0].orderNo;
        if (1 === this.orderRooms.length) {
            this.consumeInfo.roomNo = this.orderRooms[0].roomNo;
            this.consumeInfo.roomName = this.orderRooms[0].roomName;
        }
      }
    },
    submit() {
        this.loading = true;
        this.$refs.consumeForm.validate(valid => {
            if (valid) {
                console.log(this.consumeInfo);
                saveOrderConsume(this.consumeInfo).then(res => {
                    if (res.data.code === 0) {
                        this.$Message.success(res.data.msg);
                        this.saveIs = true;
                        this.isOpen = false;
                    } else {
                        this.$Message.error(res.data.code + "" + res.data.msg);
                        this.loading = false;
                    }
                });
            } else {
                this.$Message.error('');
                this.loading = false;
            }
        })  
    },
    offsetSubmit() {
        this.loading = true;
        this.$refs.offsetForm.validate(valid => {
            if (valid) {
              this.consumeInfo.orderNo = this.offsetOrderConsume.orderNo;
              this.consumeInfo.consumeTypeCode="outgoings";
              this.consumeInfo.consumeItemCode="offset";
              this.consumeInfo.offsetId=this.offsetOrderConsume.consumeId;
              this.consumeInfo.roomName=this.offsetOrderConsume.roomName;
              this.consumeInfo.roomNo=this.offsetOrderConsume.roomNo;
                console.log(this.consumeInfo);
                saveOrderConsume(this.consumeInfo).then(res => {
                    if (res.data.code === 0) {
                        this.$Message.success(res.data.msg);
                        this.saveIs = true;
                        this.isOffsetOpen = false;
                    } else {
                        this.$Message.error(res.data.code + "" + res.data.msg);
                        this.loading = false;
                    }
                });
            } else {
                this.$Message.error('');
                this.loading = false;
            }
        })  
    },
    setRoomName(val) {
        let room = this.orderRooms.filter(room => room.roomNo === val)
        console.log(room);
        
        if (room.length !== 0) this.consumeInfo.roomName = room[0].roomName;
    },
    getTips(val) {
        switch (val) {
            case 'rent':
                this.tips = '...'
                break;
            case 'commodity':
                this.tips = '1...'
                break;
            case 'cleaning_charge':
                this.tips = '/...'
                break;
            case 'damage':
                this.tips = '/...'
                break;    
            default:
                this.tips = ''
                break;
        }
    }
  },
};
</script>

